<template>
	<Dashboard>
		<div>
			<div class="mb-4 text-white">
				<h5>Izin</h5>
				<small>Izin</small>
			</div>
			
			<div class="card">
				<div class="card-body">
					
					<div id="table-wrapper" class="mt-2">
						<div class="text-right">
							<button @click="showAdd" class="btn btn-success"><i class="fas fa-plus"></i> Tambah Dokumen</button>
						</div>
						<hr>
						
						<div class="row mb-2">
							<div class="col-md-8"></div>
							<div class="col-md-4 text-right">
								<button class="btn btn-primary" @click="showFilter">
									<i class="fas fa-search"></i> Filter Izin
								</button>
							</div>
						</div>
						
						<div class="table-responsive">
							<table class="table" style="width: auto;">
								<thead>
									<tr>
										<th>Kode Izin</th>
										<th>Nama Izin</th>
										<th>Deskripsi</th>
										<th>Tanggal Dikeluarkan</th>
										<th>Tanggal Berakhir</th>
										<th>Masa Berlaku</th>
										<th>Instansi</th>
										<th>Sub Instansi</th>
										<th>Nomor Persetujuan</th>
										<th>Referensi Kode Izin</th>
										<th>Status</th>
										<th>Pembaharuan Kode Izin</th>
										<th>Dokumen</th>
										<th>Kewajiban</th>
									</tr>
								</thead>
								<tbody>
									<tr v-if="datas.length == 0">
										<td colspan="14" class="text-center">Belum Ada Izin</td>
									</tr>
									<tr v-else v-for="(v,i) in datas" v-bind:key="i">
										<td>
											<a href="javascript:void(0)" @click="showEdit(v)">{{ v.number }}</a>
										</td>
										<td>{{ v.labeled_name }}</td>
										<td>{{ v.description }}</td>
										<td>{{ formatDate(v.issuedDate) }}</td>
										<td>{{ formatDate(v.validDate) }}</td>
										<td>{{ v.validPeriod }}</td>
										<td>{{ v.labeled_instance }}</td>
										<td>{{ v.labeled_sub_instance }}</td>
										<td>{{ v.approvalNumber }}</td>
										<td>{{ v.labeled_reference }}</td>
										<td>{{ v.labeled_status }}</td>
										<td>{{ v.labeled_updatePermitCodeId }}</td>
										<td>
											<router-link target="_blank" :to="'/permit/attachment/' + v.id" class="btn btn-sm btn-primary">Dokumen</router-link>
										</td>
										<td>
											<div style="width: 150px !important">
												<router-link target="_blank" :to="'/obligation/' + v.id + '/single'" class="btn btn-sm btn-success mr-1">Kewajiban</router-link>
											</div>
										</td>
									</tr>
								</tbody>
							</table>
						</div>
					</div>
					
				</div>
			</div>
		</div>
		
		<PermitForm
		:model="model"
		:state="state"
		:instances="instances"
		:validPeriod="validPeriod"
		:documents="documents"
		:categories="categories"
		:documentTypes="documentTypes"
		:statuses="statuses"
		:users="users"
		:departments="departments"
		:documentHardcopies="documentHardcopies"
		:reportSchedules="reportSchedules"
		:onSave="onSave"
		:documentPic="documentPic"
		:documentDic="documentDic"
		:hardcopyLocations="hardcopyLocations"
		:addPIC="addPIC"
		:addHardcopy="addHardcopy"
		:addDept="addDept"
		:addSchedule="addSchedule"
		:selectedUser="selectedUser"
		:schedule="schedule"
		:selectedDepartment="selectedDepartment"
		:selectedHardcopy="selectedHardcopy"
		:removeHardcopy="removeHardcopy"
		:removeDept="removeDept"
		:removeSchedule="removeSchedule"
		:removePIC="removePIC"
		:countValidDate="countValidDate"
		:codeNames="codeNames"
		:getDocumentPermitName="getDocumentPermitName"
		:lastCode="lastCode"
		:permitNames="permitNames"
		:subInstances="subInstances"
		:checkDuplicate="checkDuplicate"
		:onDelete="onDelete"
		/>
		
		<PermitFilter
		:codeNames="codeNames"
		:getDocumentPermitName="getDocumentPermitName"
		:permitNames="permitNames"
		:state="state"
		:documents="documents"
		:subInstances="subInstances"
		:instances="instances"
		:getDocuments="getDocuments"
		/>
		
	</Dashboard>
</template>

<script>
	
	import alertMixin from '@/mixins/alert'
	import utilMixin from '@/mixins/util'
	import dataMixin from '@/mixins/document'
	import { documentAll, documentAdd, documentUpdate, documentDelete, documentCheckDuplicate, documentFilter } from '@/api/document'
	import moment from 'moment'
	import PermitForm from './Form.vue'
	import PermitFilter from './Filter.vue'
	import { documentPermitNameByCodeId } from '@/api/documentPermitName'
	import { documentLastCode } from '@/api/document'
	
	export default{
		name: 'Forestry',
		mixins: [
		alertMixin,
		dataMixin,
		utilMixin,
		],
		components:{
			PermitForm,
			PermitFilter,
		},
		data() {
			return {
				filters: null,
				subInstances: [],
				permitNames: [],
				docPermitNames: [],
				lastCode: null,
				instances: [],
				codeNames: [],
				pics: [],
				departments: [],
				statuses: [],
				hardcopyLocations: [],
				documentTypes: [],
				documentReferences: [],
				categories: [],
				users: [],
				datas: [],
				model: {
					id: null,
					number: null,
					referenceDocumentId: null,
					tableActionAuthorizationId: null,
					documentTypeId: null,
					statusId: null,
					name: null,
					description: null,
					approvalNumber: null,
					issuedDate: null,
					validDate: null,
					isActive: 0,
					hardcopyStorageTime: 0,
					hardcopyStorageTimeUnit: null,
					issuerInstanceId: null,
					approverInstanceId: null,
					categoryId: null,
					documentCode: null,
					codeId: null,
					permitId: null,
					updatePermitCodeId: null,
				},
				validPeriod: {
					time: null,
					timeUnit: null, 
				},
				documentHardcopies: [],
				documentPic: [],
				documentDic: [],
				reportSchedules: [],
				state: {
					schedule: false,
					addRef: false,
					canEdit: false,
					codeSelected: false,
					codeAvailable: -1,
				},
				schedule: {
					time: 0,
					timeUnit: 'Hari',
				},
				documents: [],
				search: {
					query: null,
				}
			}
		},
		async mounted(){
			this.model.tableActionAuthorizationId = 1;
			
			this.showLoading();
			this.instances = await this.getInstance(); 
			this.departments = await this.getDepartment(); 
			this.documentTypes = await this.getDocumentType(); 
			// this.statuses = await this.getStatus(); 
			this.hardcopyLocations = await this.getHardcopyLocation(); 
			this.categories = await this.getCategory();
			this.users = await this.getPersonInCharge();
			this.codeNames = await this.getCodeName();
			this.subInstances = await this.getSubInstance();
			this.docPermitNames = await this.getDocPermitName();
			await this.getDocuments(this.filters);
			
			$("#filter").hide();
			$(".vs__dropdown-toggle").css('border', "none !important");
			
			if(!$('#locationDrops').hasClass('show')){
				$('#locationDrops').dropdown('toggle');
			}
			if(!$('#picDrops').hasClass('show')){
				$('#picDrops').dropdown('toggle');
			}
			if(!$('#departmentDrops').hasClass('show')){
				$('#departmentDrops').dropdown('toggle');
			}
		},
		methods: {
			async getDocuments(filter = null){
				this.filters = filter;
				this.datas = [];
				this.documents = [];
				this.showLoading();
				
				try{
					let request = null;
					if(filter == null){
						request = await documentAll();
					}else{
						request = await documentFilter(filter);
					}
					
					let data = request.data;
					data.forEach((v, i) => {
						let parsed = v.document;
						parsed.labeled_status = (parsed.isActive) ? 'Aktif' : 'Tidak Aktif';
						parsed.labeled_instance = this.selectedStatus(this.instances, parsed.issuerInstanceId);
						parsed.labeled_sub_instance = this.selectedData(this.subInstances, parsed.approverInstanceId);
						parsed.labeled_name = this.selectedData(this.docPermitNames, parsed.permitId);
						parsed.labeled_reference = null;
						parsed.labeled_updatePermitCodeId = null;
						
						let documentTypes = [];
						let dics = [];
						let pics = [];
						let hardcopies = [];
						let reports = [];
						
						v.documentType.forEach((v2,i2) => {
							documentTypes.push(v2.id);
						})
						v.dic.forEach((v2,i2) => {
							dics.push(v2.documentDic.departmentId);
						})
						v.pic.forEach((v2, i2) => {
							pics.push(v2.userID);
						})
						v.hard_copies.forEach((v2,i2) => {
							hardcopies.push(v2.documentHardcopies.hardcopyLocationId);
						})
						v.reportSchedules.forEach((v2, i2) => {
							reports.push(v2);
						})
						
						parsed.documentType = documentTypes;
						parsed.documentDic = dics;
						parsed.documentPic = pics;
						parsed.documentHardcopies = hardcopies;
						parsed.reportSchedules = reports;
						
						this.documents.push({
							label: parsed.number,
							value: parsed.id,
						})
						
						this.datas.push(parsed);
					})
					this.datas.forEach((v,i) => {
						if(v.referenceDocumentId != null){
							this.documents.forEach((v2,i2) => {
								if(v2.value == v.referenceDocumentId){
									v.labeled_reference = v2.label
								}
							})
						}
						if(v.updatePermitCodeId != null){
							this.documents.forEach((v2,i2) => {
								if(v2.value == v.updatePermitCodeId){
									v.labeled_updatePermitCodeId = v2.label
								}
							})
						}
					})
					
					this.hideLoading();
				}catch(e){
					this.hideLoading();
					this.showAlert("Gagal Mendapatkan Data", "error");
					console.log(e);
					if(e.response?.status == 401){
						this.invalidSession();
					}
				}
			},
			showAdd(){
				this.state.canEdit = true;
				this.model = {
					id: null,
					number: null,
					referenceDocumentId: null,
					tableActionAuthorizationId: 1,
					documentTypeId: null,
					statusId: null,
					name: null,
					description: null,
					approvalNumber: null,
					issuedDate: null,
					validDate: null,
					isActive: 0,
					hardcopyStorageTime: 0,
					hardcopyStorageTimeUnit: null,
					issuerInstanceId: null,
					approverInstanceId: null,
					categoryId: null,
					updatePermitCodeId: null,
				};
				this.documentHardcopies = [];
				this.documentPic = [];
				this.documentDic = [];
				
				$("#modalAdd").modal('show');
			},
			onSave(){
				if(!this.formValidate()){
					this.showNotif("Data Izin Belum Lengkap");
					return;
				}
				
				this.showLoading();
				
				let model = this.model;
				if(this.validPeriod.time && this.validPeriod.timeUnit){
					model.ValidPeriod = this.validPeriod.time + " " + this.validPeriod.timeUnit;
				}else{
					this.hideLoading();
					this.showNotif("Masa Berlaku Tidak ValidPeriod");
					return;
				}
				
				let documentHardcopies = [];
				let documentPic = [];
				let documentDic = [];
				
				this.documentHardcopies.forEach((v,i) => {
					documentHardcopies.push({
						documentId: null,
						hardcopyLocationId: v,
					})
				})
				this.documentPic.forEach((v,i) => {
					documentPic.push({
						documentId: null,
						userID: v,
					})
				})
				this.documentDic.forEach((v,i) => {
					documentDic.push({
						documentId: null,
						departmentId: v,
					})
				})
				
				if(this.model.id == null){
					let request = documentAdd({
						document: model,
						documentHardcopies: documentHardcopies,
						documentPic: documentPic,
						documentDic: documentDic,
						reportSchedules: this.reportSchedules,
					});
					request
					.then((response) => {
						this.hideLoading();
						
						$("#modalAdd").modal("hide");
						this.showAlert("Izin Berhasil Disimpan");
						this.getDocuments(this.filters);
					})
					.catch((error) => {
						this.hideLoading();
						console.log(error);
						this.showNotif("Terjadi Kesalahan");
						if(e.response?.status == 401){
							this.invalidSession();
						}
					})
				}else{
					let request = documentUpdate(this.model.id, {
						document: model,
						documentHardcopies: documentHardcopies,
						documentPic: documentPic,
						documentDic: documentDic,
						reportSchedules: this.reportSchedules,
					});
					request
					.then((response) => {
						this.hideLoading();
						
						$("#modalAdd").modal("hide");
						this.showAlert("Izin Berhasil Disimpan");
						this.getDocuments(this.filters);
					})
					.catch((error) => {
						this.hideLoading();
						console.log(error);
						this.showNotif("Terjadi Kesalahan");
						if(e.response?.status == 401){
							this.invalidSession();
						}
					})
				}
			},
			formValidate(){
				let valid = true;
				['codeId', 'permitId', 'documentCode', 'approvalNumber', 'description', 'issuedDate', 'validDate', 'issuerInstanceId', 'approverInstanceId'].forEach((k) => {
					if(this.model[k] == "" || this.model[k] == null){
						valid = false;
					}
				})
				
				if(this.documentPic.length == 0 || this.documentDic.length == 0 || this.documentHardcopies.length == 0){
					valid = false;
				}
				
				return valid;
			},
			showEdit(data){
				let validPeriod = (data.validPeriod) ? data.validPeriod.split(" ") : [];
				this.getDocumentPermitName(data.codeId);
				
				this.model = {
					id: data.id,
					number: data.number,
					referenceDocumentId: data.referenceDocumentId,
					tableActionAuthorizationId: data.tableActionAuthorizationId,
					documentTypeId: data.documentTypeId,
					statusId: data.statusId,
					name: data.name,
					description: data.description,
					approvalNumber: data.approvalNumber,
					issuedDate: data.issuedDate?.split("T")?.[0],
					validDate: data.validDate?.split("T")?.[0],
					isActive: data.isActive,
					hardcopyStorageTime: data.hardcopyStorageTime,
					hardcopyStorageTimeUnit: data.hardcopyStorageTimeUnit,
					issuerInstanceId: data.issuerInstanceId,
					approverInstanceId: data.approverInstanceId,
					categoryId: data.categoryId,
					documentCode: data.documentCode,
					codeId: data.codeId,
					permitId: data.permitId,
					updatePermitCodeId: data.updatePermitCodeId,
				};
				this.documentHardcopies = data.documentHardcopies;
				this.documentPic = data.documentPic;
				this.documentDic = data.documentDic;
				this.reportSchedules = data.reportSchedules;
				this.validPeriod = {
					time: (validPeriod.length > 0) ? validPeriod[0] : '0',
					timeUnit: (validPeriod.length > 0) ? validPeriod[1] : 'Hari',
				};
				this.state.canEdit = false;
				$("#modalAdd").modal('show');
			},
			addPIC(name){
				if(!this.documentPic.includes(name)){
					this.documentPic.push(name);
				}else{
					this.showNotif("PIC Sudah Ditambahkan");
				}
			},
			removePIC(index){
				this.documentPic.splice(index,1);
			},
			addDept(name){
				if(!this.documentDic.includes(name)){
					this.documentDic.push(name);
				}else{
					this.showNotif("Departemen Sudah Ditambahkan");
				}
			},
			removeDept(index){
				this.documentDic.splice(index,1);
			},
			addHardcopy(name){
				if(!this.documentHardcopies.includes(name)){
					this.documentHardcopies.push(name);
				}else{
					this.showNotif("Hardcopy Sudah Ditambahkan");
				}
			},
			removeHardcopy(index){
				this.documentHardcopies.splice(index,1);
			},
			showReport(){
				$("#modalReport").modal("show");
			},
			addSchedule(){
				if(this.schedule.time == "" || this.schedule.timeUnit == ""){
					this.showNotif("Waktu Harus Diisi");
				}else{
					this.reportSchedules.push({
						id: null,
						documentId: null,
						time: this.schedule.time,
						timeUnit: this.schedule.timeUnit,
					});
					this.state.schedule = false;
				}
			},
			removeSchedule(index){
				this.reportSchedules.splice(index,1);
			},
			countValidDate(source = 'validDate'){
				let issuedDate = this.model.issuedDate;
				let validDate = this.model.validDate;
				let validPeriod = this.validPeriod;
				
				try{
					if(source == 'validDate'){
						this.validPeriod = this.getDateDiv(issuedDate, validDate);
					}else if(source == 'validPeriod'){
						switch(validPeriod.timeUnit){
							case "Hari":
							this.model.validDate = moment(moment(issuedDate).add(validPeriod.time, 'd')).format('YYYY-MM-DD');
							break;
							case "Bulan":
							this.model.validDate = moment(moment(issuedDate).add(validPeriod.time, 'M')).format('YYYY-MM-DD');
							break;
							case "Tahun":
							this.model.validDate = moment(moment(issuedDate).add(validPeriod.time, 'y')).format('YYYY-MM-DD');
							break;
						}
					}
				}catch(e){
					console.log(e);
				}
			},
			getDateDiv(startDate, endDate){
				try{
					if(!startDate || !endDate){
						return {
							time: null,
							timeUnit: null
						}
					}
					
					let start = moment(startDate);
					let end = moment(endDate);
					
					let year = end.diff(start, "years");
					let month = end.diff(start, "months");
					let days = end.diff(start, "days");
					
					if(month % 12 != 0){
						return {
							time: month,
							timeUnit: "Bulan",
						};
					}
					
					if(year == 0){
						if(month == 0){
							if(days == 0){
								return {
									time: 0,
									timeUnit: "Hari",
								};
							}else{
								return {
									time: days,
									timeUnit: "Hari",
								};
							}
						}else{
							return {
								time: month,
								timeUnit: "Bulan",
							};
						}
					}else{
						return {
							time: year,
							timeUnit: "Tahun",
						};
					}
				}catch(e){
					return {
						time: 0,
						timeUnit: "Hari",
					};
				}
			},
			async getDocumentPermitName(code){
				this.permitNames = [];
				this.state.codeSelected = false;
				
				try{
					let request = await documentPermitNameByCodeId(code);
					this.getLastCode(code);
					let data = request.data;
					data.forEach((v,i) => {
						this.permitNames.push({
							label: v.name,
							value: v.id,
						})
					})
				}catch(e){
					this.getLastCode(code);
					console.log(e);
				}
			},
			async getLastCode(code){
				this.lastCode = null;
				try{
					let request = await documentLastCode(code);
					this.state.codeSelected = true;
					let data = request.data;
					if(data != null && data.length > 0){
						this.lastCode = data[0].documentCode;
					}
				}catch(e){
					console.log(e);
					this.state.codeSelected = true;
				}
			},
			async checkDuplicate(){
				this.state.codeAvailable = -1;
				try{
					let request = await documentCheckDuplicate(this.model.codeId, this.model.documentCode);
					let data = request.data;
					if(!data){
						this.state.codeAvailable = 1;
					}else{
						this.state.codeAvailable = 0;
						this.showAlert("Nomor Kode Tidak Tersedia", "error");
					}
				}catch(e){
					this.state.codeAvailable = 1;
					console.log(e);
				}
			},
			onDelete(){
				this.$swal({
					title: 'Hapus Data',
					text: "Yakin ingin menghapus data ?",
					type: 'error',
					showCancelButton: true,
					confirmButtonColor: '#3085d6',
					cancelButtonColor: '#d33',
					confirmButtonText: 'Yes'
				}).then((result) => {
					if (result.value) {
						this.showLoading();
						let request = documentDelete(this.model.id);
						request
						.then((response) => {
							this.hideLoading();
							$("#modalAdd").modal("hide");
							
							this.$toast.open({
								message: 'Dokumen Berhasil Dihapus !',
								type: 'info',
								position: 'top-right',
							});
							
							this.getDocuments(this.filters);
						})
						.catch((error) => {
							this.hideLoading();
							this.showNotif("Terjadi Kesalahan");
							console.log(error);
						})
					}
				});
			},
			showFilter(){
				$("#modalFilter").modal("show");
			},
			async invalidSession(){
				alert("Sesi sudah tidak valid, silahkan login");
				await this.$store.dispatch("auth/authLogout");
				this.$router.replace("/login");
			}
		}
	}
</script>