<template>
	<div>
		
		<div class="modal fade" id="modalAdd" tabindex="-1" role="dialog" aria-labelledby="modalStatus" aria-hidden="true" data-backdrop="static" data-keyboard="false">
			<div class="modal-dialog modal-lg" role="document">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title" id="exampleModalLongTitle">Detail Izin</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">
						
						<div class="row">
							<div class="col-md-6">
								<div class="form-group">
									<label>Nama Kode</label>
									<v-select :options="codeNames" :reduce="codeName => codeName.value" @input="getDocumentPermitName" :disabled="!state.canEdit" class="form-control" v-model="model.codeId"></v-select>
								</div>
							</div>
							<div class="col-md-6">
								<div class="form-group">
									<label>Nama Izin</label>
									<v-select :disabled="!state.canEdit" :options="permitNames" :reduce="permitName => permitName.value" :readonly="!state.codeSelected" v-model="model.permitId" class="form-control"></v-select>
								</div>
							</div>
							<div class="col-md-6">
								<div class="form-group">
									<label v-if="lastCode == null">Nomor Kode</label>
									<label v-else>Nomor Kode (kode terakhir :<span class="text-primary">{{ lastCode }}</span>)</label>
									<input type="text" @keyup="checkDuplicate" :disabled="!state.canEdit" :readonly="!state.codeSelected" :class="'form-control ' + ((model.documentCode != null &&  state.codeAvailable == 0) ? 'is-invalid' : '')" v-model="model.documentCode">
									<p class="text-danger" v-if="(model.documentCode != null &&  state.codeAvailable == 0)">Kode sudah pernah dipakai</p>
								</div>
							</div>
							<div class="col-md-6">
								<div class="form-group">
									<label>Nomor Persetujuan</label>
									<input :disabled="!state.canEdit" type="text" class="form-control" v-model="model.approvalNumber">
								</div>
							</div>
							<div class="col-md-12">
								<div class="form-group">
									<label>Deskripsi</label>
									<textarea :disabled="!state.canEdit" class="form-control" v-model="model.description"></textarea>
								</div>
							</div>
							<div class="col-md-6">
								<div class="form-group">
									<label>Tanggal Dikeluarkan</label>
									<input :disabled="!state.canEdit" type="date" class="form-control" v-model="model.issuedDate">
								</div>
								<div class="form-group">
									<label>Tanggal Berakhir</label>
									<input :disabled="!state.canEdit" type="date" class="form-control" @change="countValidDate('validDate')" v-model="model.validDate">
								</div>
								<div class="form-group">
									<label>Masa Berlaku</label>
									<div class="input-group">
										<input :disabled="!state.canEdit" type="number" step="1" class="form-control" v-model="validPeriod.time" @keyup="countValidDate('validPeriod')" @change="countValidDate('validPeriod')">
										<div class="input-group-append">
											<select :disabled="!state.canEdit" class="form-control" v-model="validPeriod.timeUnit" @change="countValidDate('validPeriod')">
												<option value="Hari">Hari</option>
												<option value="Bulan">Bulan</option>
												<option value="Tahun">Tahun</option>
											</select>
										</div>
									</div>
								</div>
								<div class="form-group">
									<label>Referensi Kode Izin</label>
									<v-select :options="documents" :reduce="document => document.value" :disabled="!state.canEdit" class="form-control" v-model="model.referenceDocumentId"></v-select>
								</div>
							</div>
							<div class="col-md-6">
								<div class="form-group">
									<label>Instansi</label>
									<v-select :options="instances" :reduce="instance => instance.value" :disabled="!state.canEdit" class="form-control" v-model="model.issuerInstanceId"></v-select>
								</div>
								<div class="form-group">
									<label>Sub Instansi</label>
									<v-select :options="subInstances" :reduce="subInstance => subInstance.value" :disabled="!state.canEdit" class="form-control" v-model="model.approverInstanceId"></v-select>
								</div>
								<!-- <div class="form-group">
									<label>Status</label>
									<v-select :options="statuses" :reduce="status => status.value" :disabled="!state.canEdit" class="form-control" v-model="model.statusId"></v-select>
								</div> -->
								<div class="form-group">
									<label>Pembaharuan Kode Izin</label>
									<v-select :options="documents" :reduce="document => document.value" :disabled="!state.canEdit" class="form-control" v-model="model.updatePermitCodeId"></v-select>
								</div>
							</div>
							<div class="col-md-6">
								<div class="form-group">
									<label>Penanggung Jawab</label>
									<div v-for="(v,i) in documentPic" :key="i">
										<div class="row mb-1">
											<div class="col-md-10">
												<h4>{{ selectedUser(users, v) }}</h4>
											</div>
											<div class="col-md-2">
												<button v-show="state.canEdit" @click="removePIC(i)" class="btn btn-sm btn-danger"><i class="fas fa-times-circle"></i></button>
											</div>
										</div>
									</div>
									<div class="btn-group">
										<button id="picDrops" type="button" v-show="state.canEdit" class="btn btn-success dropdown-toggle btn-block" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
											Tambah PIC
										</button>
										<div class="dropdown-menu" style="right: 30px !important">
											<a href="#" class="dropdown-item" v-for="(p, ii) in users" :key="ii" @click="addPIC(p.value)">{{ p.label }}</a>
										</div>
									</div>
								</div>
								<div class="form-group">
									<label>Jadwal Lapor</label>
									<div v-for="(v,i) in reportSchedules" :key="i">
										<div class="row mb-1">
											<div class="col-md-10">
												<h4>{{ v.time }} {{ v.timeUnit }}</h4>
											</div>
											<div class="col-md-2">
												<button v-show="state.canEdit" @click="removeSchedule(i)" class="btn btn-sm btn-danger"><i class="fas fa-times-circle"></i></button>
											</div>
										</div>
									</div>
									<button v-if="state.canEdit && !state.schedule" @click="state.schedule = true" class="btn btn-success btn-block">Tambah Jadwal</button>
									<div class="input-group" v-if="state.schedule">
										<input :disabled="!state.canEdit" type="number" step="1" class="form-control" v-model="schedule.time">
										<div class="input-group-append">
											<select :disabled="!state.canEdit" class="form-control" v-model="schedule.timeUnit">
												<option value="Hari">Hari</option>
												<option value="Minggu">Minggu</option>
												<option value="Bulan">Bulan</option>
												<option value="Tahun">Tahun</option>
											</select>
										</div>
									</div>
									<div v-if="state.schedule" class="row mt-2">
										<div class="col-md-6">
											<button @click="addSchedule" class="btn btn-success btn-block">Simpan</button>
										</div>
										<div class="col-md-6">
											<button @click="state.schedule = false" class="btn btn-danger btn-block">Batal</button>
										</div>
									</div>
								</div>
							</div>
							<div class="col-md-6">
								<div class="form-group">
									<label>Departement</label>
									<div v-for="(v,i) in documentDic" :key="i">
										<div class="row mb-1">
											<div class="col-md-10">
												<h4>{{ selectedDepartment(departments,v) }}</h4>
											</div>
											<div class="col-md-2">
												<button v-show="state.canEdit" @click="removeDept(i)" class="btn btn-sm btn-danger"><i class="fas fa-times-circle"></i></button>
											</div>
										</div>
									</div>
									<div class="btn-group">
										<button id="departmentDrops" v-show="state.canEdit" type="button" class="btn btn-success dropdown-toggle btn-block" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
											Tambah Departemen
										</button>
										<div class="dropdown-menu" style="right: 30px !important">
											<a href="#" class="dropdown-item" v-for="(p, ii) in departments" :key="ii" @click="addDept(p.value)">{{ p.label }}</a>
										</div>
									</div>
								</div>
								<div class="form-group">
									<label>Lokasi Hardcopy</label>
									<div v-for="(v,i) in documentHardcopies" :key="i">
										<div class="row mb-1">
											<div class="col-md-10">
												<h4>{{ selectedHardcopy(hardcopyLocations,v) }}</h4>
											</div>
											<div class="col-md-2">
												<button v-show="state.canEdit" @click="removeHardcopy(i)" class="btn btn-sm btn-danger"><i class="fas fa-times-circle"></i></button>
											</div>
										</div>
									</div>
									<div class="btn-group">
									<button id="locationDrops" v-show="state.canEdit" type="button" class="btn btn-success dropdown-toggle btn-block" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
										Tambah Lokasi
									</button>
									<div v-show="state.canEdit" class="dropdown-menu" style="right: 30px !important">
										<a href="javascript:void(0)" class="dropdown-item" v-for="(p, ii) in hardcopyLocations" :key="ii" @click="addHardcopy(p.value)">{{ p.label }}</a>
									</div>
								</div>
								</div>
							</div>
						</div>

					</div>

					<div class="modal-footer">
						<router-link v-if="model.id != null" :to="'permit/'+model.id+'/reminder'" class="btn btn-primary">Pengingat Izin</router-link>
						<button v-if="!state.canEdit || model.id == null" type="button" class="btn btn-secondary" data-dismiss="modal">Batal</button>
						<button v-else="" type="button" class="btn btn-secondary" @click="state.canEdit = false">Batal</button>
						<button v-show="state.canEdit" @click="onSave" type="button" class="btn btn-primary">Simpan</button>
						<button v-show="!state.canEdit" @click="state.canEdit = true" type="button" class="btn btn-warning">Edit</button>
						<button v-show="!state.canEdit" @click="onDelete" type="button" class="btn btn-danger">Hapus</button>
					</div>
				</div>
			</div>
		</div>

	</div>
</template>

<script type="text/javascript">
	export default{
		name: 'PermitForm',
		props: [
		'model',
		'state',
		'instances',
		'validPeriod',
		'documents',
		'categories',
		'documentTypes',
		'statuses',
		'users',
		'departments',
		'documentHardcopies',
		'reportSchedules',
		'onSave',
		'documentPic',
		'documentDic',
		'hardcopyLocations',
		'addPIC',
		'addHardcopy',
		'addDept',
		'addSchedule',
		'selectedUser',
		'schedule',
		'selectedDepartment',
		'selectedHardcopy',
		'removeHardcopy',
		'removeDept',
		'removeSchedule',
		'removePIC',
		'countValidDate',
		'codeNames',
		'getDocumentPermitName',
		'lastCode',
		'permitNames',
		'subInstances',
		'checkDuplicate',
		'onDelete',
		],
	}
</script>

<style>
	.dropdown-menu{
		overflow-y: auto;
		max-height: 150px !important;
	}
</style>