<template>
	<div>
		<div class="modal fade" id="modalFilter" tabindex="-1" role="dialog" aria-labelledby="modalStatus" aria-hidden="true">
			<div class="modal-dialog modal-lg" role="document">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title" id="exampleModalLongTitle">Filter Izin</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					
					<div class="modal-body">
						
						<div class="row">
							<div class="col-md-6">
								<div class="form-group">
									<label>Nama Kode</label>
									<v-select :options="codeNames" :reduce="codeName => codeName.value" @input="getDocumentPermitName" class="form-control" v-model="filter.codeId"></v-select>
								</div>
							</div>
							<div class="col-md-6">
								<div class="form-group">
									<label>Nama Izin</label>
									<v-select :options="permitNames" :reduce="permitName => permitName.value" :readonly="!state.codeSelected" v-model="filter.permitId" class="form-control"></v-select>
								</div>
							</div>
							<div class="col-md-6">
								<div class="form-group">
									<label>Nomor Kode</label>
									<input type="text" :readonly="!state.codeSelected" :class="'form-control ' + ((filter.documentCode != null &&  state.codeAvailable == 0) ? 'is-invalid' : '')" v-model="filter.documentCode">
									<p class="text-danger" v-if="(filter.documentCode != null &&  state.codeAvailable == 0)">Kode sudah pernah dipakai</p>
								</div>
							</div>
							<div class="col-md-6">
								<div class="form-group">
									<label>Nomor Persetujuan</label>
									<input type="text" class="form-control" v-model="filter.approvalNumber">
								</div>
							</div>
							<div class="col-md-12">
								<div class="form-group">
									<label>Deskripsi</label>
									<textarea class="form-control" v-model="filter.description"></textarea>
								</div>
							</div>
							<div class="col-md-6">
								<!-- <div class="form-group">
									<label>Tanggal Dikeluarkan</label>
									<input type="date" class="form-control" v-model="filter.issuedDate">
								</div>
								<div class="form-group">
									<label>Tanggal Berakhir</label>
									<input type="date" class="form-control" @change="countValidDate('validDate')" v-model="filter.validDate">
								</div> -->
								<div class="form-group">
									<label>Masa Berlaku</label>
									<div class="input-group">
										<input type="number" step="1" class="form-control" v-model="validPeriod.time" @change="countValidDate('validPeriod')">
										<div class="input-group-append">
											<select class="form-control" v-model="validPeriod.timeUnit" @change="countValidDate('validPeriod')">
												<option value="Hari">Hari</option>
												<option value="Bulan">Bulan</option>
												<option value="Tahun">Tahun</option>
											</select>
										</div>
									</div>
								</div>
								<div class="form-group">
									<label>Referensi Kode Izin</label>
									<v-select :options="documents" :reduce="document => document.value" class="form-control" v-model="filter.referenceDocumentId"></v-select>
								</div>
							</div>
							
							<div class="col-md-6">
								<div class="form-group">
									<label>Instansi</label>
									<v-select :options="instances" :reduce="instance => instance.value" class="form-control" v-model="filter.issuerInstanceId"></v-select>
								</div>
								<div class="form-group">
									<label>Sub Instansi</label>
									<v-select :options="subInstances" :reduce="subInstance => subInstance.value" class="form-control" v-model="filter.approverInstanceId"></v-select>
								</div>
								<div class="form-group">
									<label>Pembaharuan Kode Izin</label>
									<v-select :options="documents" :reduce="document => document.value" class="form-control" v-model="filter.updatePermitCodeId"></v-select>
								</div>
							</div>
							
							<div class="col-md-12">
								<label>Tanggal Dikeluarkan</label>
							</div>
							<div class="col-md-6">
								<input type="date" class="form-control" v-model="filter.startIssuedDate">
							</div>
							<div class="col-md-6">
								<input type="date" class="form-control" v-model="filter.endIssuedDate">
							</div>
							
							<div class="col-md-12 mt-3">
								<label>Tanggal Berakhir</label>
							</div>
							<div class="col-md-6">
								<input type="date" class="form-control" v-model="filter.startValidDate">
							</div>
							<div class="col-md-6">
								<input type="date" class="form-control" v-model="filter.endValidDate">
							</div>
						</div>
						
					</div>
					
					<div class="modal-footer">
						<button type="button" class="btn btn-secondary" data-dismiss="modal">Batal</button>
						<button type="button" @click="filterData" class="btn btn-primary">Filter</button>
						<button type="button" @click="exportData" class="btn btn-success">Export</button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">
	
	import alertMixin from '@/mixins/alert'
	import { documentExport } from '@/api/document'
	import axios from 'axios'
	import { saveAs } from 'file-saver'
	
	export default{
		name: 'PermitFilter',
		mixins: [
		alertMixin,
		],
		data(){
			return{
				filter: {
					approvalNumber: null,
					approverInstanceId: null,
					codeId: null,
					description: null,
					documentCode: null,
					issuedDate: null,
					issuerInstanceId: null,
					referenceDocumentId: null,
					updatePermitCodeId: null,
					validDate: null,
					startIssuedDate: null,
					endIssuedDate: null,
					startValidDate: null,
					endValidDate: null,
				},
				validPeriod: {
					time: null,
					timeUnit: null, 
				},
			}
		},
		props: [
		"documents",
		"codeNames",
		"getDocumentPermitName",
		"permitNames",
		"state",
		"subInstances",
		"instances",
		"getDocuments",
		],
		methods: {
			async filterData(){
				let filter = this.filter;
				if(this.validPeriod.time && this.validPeriod.timeUnit){
					filter.validPeriod = this.validPeriod.time + " " + this.validPeriod.timeUnit;
				}
				await this.getDocuments(filter);
				$("#modalFilter").modal('hide');
			},
			countValidDate(source = 'validDate'){
			},
			async exportData(){
				this.showLoading();
				
				try{
					let filter = this.filter;
					if(this.validPeriod.time && this.validPeriod.timeUnit){
						filter.validPeriod = this.validPeriod.time + " " + this.validPeriod.timeUnit;
					}
					let response = await axios.post(process.env.VUE_APP_API_URL + '/generate_excel', filter,
					{
						responseType: 'blob',
						headers: {
							"Authorization": "Bearer " + this.$store.state.auth.user?.token
						}
					}
					);
					this.hideLoading();
					saveAs(new Blob([response.data]), "export.xlsx");
				}catch(e){
					this.hideLoading();
					this.showNotif('Gagal mengexport data');
					if(e.response?.status == 401){
						this.invalidSession();
					}
				}
			},
			async invalidSession(){
				alert("Sesi sudah tidak valid, silahkan login");
				await this.$store.dispatch("auth/authLogout");
				this.$router.replace("/login");
			}
		}
	}
</script>